@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@300;400&family=Fira+Sans+Condensed:wght@200;300;400;500;600;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  // font-family: 'Dosis', sans-serif;
  font-family: "Fira Sans Condensed", sans-serif;
}

:root {
  --primaryColor: #eaa45c;   //eaa45c
  --secondaryColor: #16a085;
  --thirdColor: #ffe8e1;
  --forthColor: #fff1ed;
  --cardColor: #ffefe9;
  --LocColor: #f0e2df;
  --bgColor: #ffeae0;
  --smallSize: 13px;
  --normalSize: 18px;
  --mediumSize: 25px;
  --largeSize: 35px;
  --textColor: rgb(48, 48, 48);
  --whiteColor: hsl(0, 0%, 100%);
  --borderRadius: 10px;
  --boxShadow: 0 2px 4px 4px #f8e0d4d7;
  --greyColor: rgb(180, 180, 180);
  --greenColor: rgb(56, 223, 56);
  --yellowColor: rgb(255, 170, 0);
}

body {
  background: #ffff;
}

html {
  font-size: 90%;
  scroll-behavior: smooth;
}

input,
textarea {
  font-size: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
}

li {
  list-style: none;
  cursor: pointer;
}

.flex {
  display: flex;
  align-items: center;
}

.grid {
  display: grid;
  gap: 2rem;
  align-items: center;
}

img {
  height: auto;
  width: 100%;
}

p {
  font-family: "Dosis", sans-serif;
}

.section {
  padding: 4rem 0 2rem;
}

.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.btn {
  border: none;
  outline: none;
  border-radius: var(--borderRadius);
  cursor: pointer;
  padding: 10px 15px;
}

.icon {
  font-size: 20px;
  cursor: pointer;
}

.sectionIntro {
  text-align: center;
  width: 100%;
  margin-bottom: 2rem;
  background: var(--secondaryColor);
  padding: 2rem 1rem;
  border-radius: var(--borderRadius);

  .secTitle {
    font-size: var(--mediumSize);
    color: var(--textColor);
    font-weight: 700;
  }

  .subTitle {
    font-weight: 600;
    color: var(--textColor);
    opacity: 0.7;
    display: block;
    padding: 0.3rem 0;
  }

  img {
    max-width: 80px;
  }
}

// success message
.success {
  display: block;
  background: var(--greenColor);
  color: var(--whiteColor);
  margin-bottom: 1rem;
  padding: 0.5rem;
  width: max-content;
  border-radius: 3px;
}
// Blank message
.blank {
  display: block;
  background: var(--primaryColor);
  color: var(--bgColor);
  margin-bottom: 1rem;
  padding: 0.5rem;
  width: max-content;
  border-radius: 3px;
}

.messageConatainer,
.messageConatainerHome {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(255, 225, 225, 0.44);
  backdrop-filter: blur(15px);
  z-index: 100;
  justify-content: center;
  align-items: center;

  .messageCard {
    background: var(--cardColor);
    border-radius: 10px;
    padding: 1rem;
    height: 200px;
    width: 290px;
    text-align: center;
    box-shadow: var(--boxShadow);
    border: 1px solid var(--whiteColor);

    .checkIcon {
      width: 20%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 1rem auto;
      animation: moveCart 3s ease-in-out infinite;
    }
    .checkIconHome {
      width: 20%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 1rem auto;
    }

    @keyframes moveCart {
      0% {
        transform: translateX(0px);
      }
      50% {
        transform: translateX(30px);
      }
      100% {
        transform: translateX(0px);
      }
    }

    .btn {
      display: block;
      background: var(--secondaryColor);
      color: var(--primaryColor);
      font-weight: 600;
      margin: 1rem;

      &:hover {
        background: var(--primaryColor);
        color: var(--cardColor);
        transition: 0.5s ease;
      }
    }
  }
  .displayNone {
    display: none;
    transition: 0.3s ease;
  }
}

// Header CSS =============>
.header {
  position: fixed !important;
  width: 100%;
  margin: auto;
  padding: 1rem 1.5rem; //padding will increse at a certain width.
  justify-content: space-between;
  z-index: 1000;
  border-bottom: 1px solid var(--secondaryColor);

  .logoDiv {
    cursor: pointer;
    .logo {
      color: var(--primaryColor);
      font-weight: 700;
      font-size: var(--normalSize);
    }
  }

  @media screen and (max-width: 660px) {
    .navBar {
      position: fixed;
      top: -100rem;
      right: 0;
      left: 50%;
      width: 80%;
      border-radius: var(--borderRadius);
      text-align: center;
      transform: translate(-50%);
      background: var(--cardColor);
      box-shadow: 0 2px 4px 4px #f8e0d4d7;
      z-index: 100;
      padding: 2rem;
      transition: 0.4s;

      .closeNavbar {
        position: absolute;
        top: -10px;
        left: -50%;
        right: -50%;
        margin-bottom: 1rem;

        .icon {
          color: var(--primaryColor);
          transition: 0.5s;
          font-size: 25px;
        }

        .icon:hover {
          transform: rotate(180deg);
        }
      }

      .navLists {
        justify-content: center;
        flex-direction: column;

        .navItem {
          color: var(--BgColor);
          padding: 0.7rem 0;

          .navLink {
            color: var(--textColor);
            font-weight: 600;
            &:hover {
              color: var(--primaryColor);
            }
          }
        }

        .navBarText {
          display: block;
          border-top: 1px dashed var(--primaryColor);
          padding-top: 1rem;
          margin-top: 0.5rem;
          p {
            color: var(--textColor);
            opacity: 0.6;
          }
        }
      }
    }

    .show {
      top: 6rem;
    }
  }

  .headerIcons {
    gap: 1rem;
    .icon {
      background: var(--cardColor);
      border-radius: 10px;
      padding: 5px;
      box-shadow: var(--boxShadow);
      border: 1px solid var(--whiteColor);
      &:hover {
        color: var(--primaryColor);
      }
    }

    .notDiv {
      position: relative;
      a {
        color: var(--textColor);
        &:hover {
          color: var(--primaryColor);
        }
      }

      .count {
        position: absolute;
        height: 15px;
        width: 15px;
        padding: 2px;
        background: var(--primaryColor);
        border-radius: 50%;
        color: var(--whiteColor);
        right: 0px;
        top: -5px;
        font-size: 10px;
        font-weight: 700;
        text-align: center;
      }
    }

    .contactNumber {
      position: relative;
      .phoneCard {
        display: none;
        position: absolute;
        background: var(--primaryColor);
        border: 1px solid var(--primaryColor);
        padding: 4px 15px;
        width: max-content;
        border-radius: var(--borderRadius);
        right: -50%;
        color: var(--whiteColor);
        .icon {
          color: var(--whiteColor);
        }

        h3 {
          font-weight: 500;
        }
      }
    }

    .locations {
      position: relative;
      .locationsList {
        top: 150%;
        position: absolute;
        right: -75%;
        width: 180px;
        height: 200px;
        padding: 1rem 0;
        border-radius: var(--borderRadius);
        background: var(--cardColor);
        box-shadow: var(--boxShadow);
        display: none;
        align-items: center;
        justify-content: center;
        overflow-y: auto;

        .singleLocation {
          width: 100%;
          justify-content: center;
          margin: auto;
          padding: 0.2rem 1.5rem;
          color: var(--textColor);
          font-family: "Dosis", sans-serif;
          .icon {
            color: var(--greyColor);
            font-size: var(--smallSize);
          }
        }

        .singleLocation:nth-child(odd) {
          background: var(--LocColor);
        }

        .closeList {
          position: absolute;
          top: 0;
          right: 0;

          .icon {
            color: var(--primaryColor);
            transition: 0.5s;
            font-size: 20px;
          }

          .icon:hover {
            transform: rotate(180deg);
          }
        }
      }
    }
    .loginbtn {
      background: var(--yellowColor);
      button .btn {
        background: var(--yellowColor);
        color: var(--whiteColor);
      }
    }
  }

  .toggleNavbar {
    margin-left: 0.5rem;
    .icon {
      font-size: 26px;
      color: var(--textColor);
      &:hover {
        color: var(--primaryColor);
      }
    }
  }
}

.headerBG {
  background: var(--secondaryColor);
}

// HOEME SECTION ========================>
.home {
  padding: 6rem 1.5rem 4rem;
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;

  .sectionContent {
    position: relative;
    gap: 2rem;
    padding: 1rem 0;

    .homeText {
      .title {
        font-weight: 700;
        font-size: var(--largeSize);
        span {
          color: var(--primaryColor);
          text-decoration: underline;
        }
      }

      p {
        display: block;
        padding: 1rem 0;
        color: var(--textColor);
        font-weight: 400;
        font-size: var(--normalSize);
      }

      .btn {
        background: var(--primaryColor);
        padding: 10px 20px;
        color: var(--whiteColor);
        font-weight: 600;
        border: 1px solid transparent;

        .icon {
          margin-left: 5px;
          transition: 0.4s;
        }

        &:hover {
          background: var(--cardColor);
          color: var(--primaryColor);
          border: 1px solid var(--primaryColor);

          .icon {
            transform: translateX(5px);
          }
        }
      }
    }

    .homeImage {
      width: 100%;
      height: 100%;
      animation: rot 5s infinite;
      @keyframes rot {
        0% {
          transform: translateY(0);
        }
        25% {
          transform: translateY(10px);
        }
        50% {
          transform: translateY(0px);
        }
        75% {
          transform: translateY(10px);
        }
        100% {
          transform: translateY(0px);
        }
      }
    }

    .floatingImg1,
    .floatingImg2,
    .floatingImg3 {
      display: none;
      position: absolute;
      max-width: 150px;
    }
  }
}

// CATEGORIES SECTION =====================>
.categoriesDiv {
  padding: 2rem;
  width: 100%; // at a wider width it will be small like 70%
  .sectionContent {
    background: var(--cardColor);
    border-radius: var(--borderRadius);
    box-shadow: var(--boxShadow);
    padding: 1rem 1rem 0;
    justify-content: center;
    margin: auto;

    .singleCat {
      display: grid;
      text-align: center;
      align-items: center;
      justify-content: center;
      gap: 10px;
      padding: 1rem;

      img {
        justify-self: center;
        align-self: center;
        max-width: 90px;
        padding: 0.5rem;
      }

      .catTitle {
        font-weight: 700;
        font-size: var(--normalSize);
        color: var(--textColor);
      }

      .btn {
        background: transparent;
        border: 1px solid var(--primaryColor);
        color: var(--textColor);
        font-weight: 500;
        &:hover {
          background: var(--primaryColor);
          border: 1px solid transparent;
          color: var(--whiteColor);
        }
      }
    }

    .singleCat:not(:last-child) {
      padding-bottom: 2rem;
      border-bottom: 1px solid var(--secondaryColor);
    }
  }
}

// ABOUT SECTION =========================>
.about {
  width: 100%;
  .sectionContent {
    padding: 1rem;

    .aboutImage {
      // order: 1; this will apply at a wider screen
      width: 100%;
      height: 100%;
      animation: rot 5s infinite;
      @keyframes rot {
        0% {
          transform: translateY(0);
        }
        25% {
          transform: translateY(10px);
        }
        50% {
          transform: translateY(0px);
        }
        75% {
          transform: translateY(10px);
        }
        100% {
          transform: translateY(0px);
        }
      }
    }

    .aboutText {
      .title {
        font-size: 1.5rem;
        font-weight: 700;
        color: var(--textColor);
        padding-bottom: 1rem;
      }

      .aboutList,
      .singleInfo {
        justify-content: center;
        .singleInfo {
          .smallImage {
            margin-right: 10px;
            background: var(--whiteColor);
            border-radius: 50%;
            padding: 0.5rem;

            img {
              max-width: 70px;
            }
          }

          .desc {
            h5 {
              font-weight: 600;
              font-size: var(--normalSize);
              color: var(--textColor);
              opacity: 0.9;
            }
          }
        }
      }
    }
  }
}

// POPULAR FOOD ITEM ====================>
.popular {
  .sectionContent {
    position: relative;
    padding-bottom: 2rem;
    &::after {
      content: "";
      position: absolute;
      background: var(--secondaryColor);
      height: 50%;
      width: 100%;
      bottom: 0;
      border-radius: var(--borderRadius);
    }
    .sectionIntro {
      text-align: center;
      padding-bottom: 2rem;
      background: none;
      h2 {
        color: var(--textColor);
        font-size: var(--mediumSize);
      }

      p {
        font-weight: 500;
      }
    }

    .contentWrapper {
      margin-top: 1rem;

      .content {
        position: relative;
        padding: 0 1.5rem;
        .swiper-wrapper {
          padding: 0.1rem;

          .singleItem {
            position: relative;
            background: var(--cardColor);
            border: 1px solid transparent;
            padding: 1rem;
            border-radius: var(--borderRadius);
            box-shadow: var(--boxShadow);
            text-align: center;
            transition: 0.3s ease;
            &:hover {
              background: var(--secondaryColor);
              border: 1px solid var(--whiteColor);
            }

            .rating {
              position: absolute;
              top: 5%;
              right: 5%;
              background: var(--whiteColor);
              border-radius: var(--borderRadius);
              padding: 5px 10px;
              font-weight: 600;
              color: var(--textColor);

              .icon {
                font-size: var(--smallSize);
                color: orange;
              }
            }

            .imgDiv {
              justify-content: center;
              height: 150px;
              width: 100%;
              overflow: hidden;
              margin-top: 2.5rem;
              border-radius: var(--borderRadius);

              a {
                height: 100%;
                width: 100%;
                img {
                  height: 100%;
                  width: auto;
                }
              }
            }

            .foodTitle {
              font-weight: 700;
              color: var(--textColor);
              font-size: var(--normalSize);
              padding: 1rem 0 0.5rem;
            }

            p {
              max-width: 200px;
              margin: auto;
              font-weight: 600;
              color: var(--textColor);
              opacity: 0.7;
            }

            .priceDiv {
              padding: 1rem 0;
              margin: auto;
              justify-content: center;
              .price {
                color: var(--primaryColor);
                font-weight: 700;
                font-size: var(--normalSize);
                display: block;
              }

              .detailsLink {
                text-decoration: underline;
                cursor: pointer;
                margin-left: 1rem;
                color: var(--textColor);
                &:hover {
                  color: var(--primaryColor);
                }
              }
            }
          }
        }
      }
    }
  }
}

// REVIEW SECTION =========================>
.review {
  .sectionContent {
    .secText {
      .secTitle {
        h2 {
          color: var(--textColor);
          font-weight: 700;
          padding-bottom: 1rem;
          font-size: var(--mediumSize);

          span {
            color: var(--primaryColor);
          }
        }
      }

      .content {
        .singleCustomer {
          width: 100%;
          p {
            font-weight: 600;
            color: var(--textColor);
            opacity: 0.7;
          }

          .customerDetails {
            margin: 1rem 0;
            .img {
              border-radius: 50%;
              overflow: hidden;
              height: 50px;
              width: 50px;
              margin-right: 1rem;
              border: 4px solid var(--whiteColor);
              img {
                object-fit: cover;
                height: 100%;
              }
            }

            .name {
              font-weight: 600;
              font-size: var(--normalSize);
              color: var(--textColor);
              line-height: 15px;
              span {
                font-size: var(--smallSize);
                color: var(--primaryColor);
              }
            }
          }
        }
      }

      .records {
        margin-top: 2rem;
        gap: 2rem;
        background: var(--secondaryColor);
        padding: 1rem;
        flex-direction: column;
        justify-content: space-evenly;
        border-top-right-radius: 5rem;
        border-top-left-radius: var(--borderRadius);
        border-bottom-left-radius: var(--borderRadius);
        border-bottom-right-radius: var(--borderRadius);

        .leftDiv {
          gap: 1rem;
          img {
            max-width: 50px;
            height: auto;
          }

          h1 {
            font-size: var(--mediumSize);
            color: var(--primaryColor);
            font-weight: 700;
          }

          span {
            color: var(--textColor);
            font-weight: 600;
          }
        }
      }
    }

    .secImage {
      img {
        width: 90%;
        max-width: 400px;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

// SUBCRIBE SECTION =======================>
.newsLetter {
  .sectionContent {
    flex-direction: column;
    justify-content: center;
    gap: 1rem;

    h1 {
      flex-basis: 50%;
      font-size: var(--mediumSize);
      color: var(--textColor);
      font-weight: 700;

      span {
        color: var(--primaryColor);
      }
    }

    form {
      flex-basis: 50%;
      width: 100%;
      .input {
        width: 100%;
        justify-content: space-between;
        background: var(--secondaryColor);
        padding: 1rem;
        border-radius: var(--borderRadius);
        input {
          background: none;
          border: none;
          outline: none;
          font-weight: 600;
          color: var(--textColor);
          &::placeholder {
            font-weight: 400;
          }
        }

        .btn {
          background: var(--primaryColor);
          color: var(--whiteColor);
          font-weight: 600;
        }
      }
    }
  }
}

// FOOTER SECTION ======================>
.footer {
  .sectionContent {
    padding: 2rem 0;
    border-top: 2px solid var(--secondaryColor);

    h3 {
      color: var(--textColor);
      font-weight: 600;
      padding-bottom: 0.5rem;
    }

    p {
      font-weight: 600;
      color: var(--textColor);
      opacity: 0.8;
    }

    .footerIntro {
      .logoDiv {
        cursor: pointer;
        .logo {
          color: var(--primaryColor);
          font-weight: 700;
          font-size: var(--normalSize);
        }
      }

      p {
        padding: 1rem 0;

        .phone {
          display: inline-block;
          color: var(--primaryColor);
          font-weight: 700;
        }
      }

      .socials {
        .icon {
          color: var(--textColor);
          &:hover {
            color: var(--primaryColor);
          }
        }
      }
    }

    .quickLinks {
      .navLink {
        display: inline-block;
        color: var(--textColor);
        opacity: 0.8;
        font-weight: 500;
        font-size: var(--samll);
        padding: 0.2rem 0;
        transition: 0.3s ease;
        &:hover {
          color: var(--primaryColor);
          transform: translateX(10px);
        }
      }
    }
  }
}

// COPYRIGHT DIV ============================>
.copyrightDiv {
  width: 100%;
  padding: 1rem;
  background: var(--secondaryColor);
  text-align: center;
  font-size: 13px;
}

// ===================== MENU PAGE CSS ================================>
.menuPage {
  padding-top: 6rem;
  .secContent {
    .optionMenu {
      justify-content: center;
      gap: 1rem;

      .option {
        display: grid;
        align-items: center;
        justify-content: center;
        padding-top: 1rem;
        height: max-content;
        width: 50px;
        padding: 8px;
        background: var(--cardColor);
        border-radius: var(--borderRadius);
        box-shadow: var(--boxShadow);
        border: 1px dashed transparent;
        cursor: pointer;
        img {
          width: 25px;
          margin: auto;
        }

        small {
          padding: 0.5rem 0;
          font-size: var(--smallSize);
          font-weight: 600;
          color: var(--textColor);
          opacity: 0.9;
        }

        &:hover {
          background: var(--secondaryColor);
          border: 1px dashed var(--primaryColor);
          small {
            color: var(--primaryColor);
          }
        }
      }

      .categoryActive {
        background: var(--secondaryColor);
        border: 1px dashed var(--primaryColor);
        small {
          color: var(--primaryColor);
        }
      }
    }

    .allItems {
      padding: 3rem 0;
      .categoryWrapper {
        .singleItem {
          position: relative;
          background: var(--cardColor);
          padding: 1rem;
          border-radius: var(--borderRadius);
          box-shadow: var(--boxShadow);
          text-align: center;
          border: 1px solid var(--whiteColor);

          &:hover {
            background: var(--secondaryColor);
            border: 1px solid var(--whiteColor);

            .price {
              background: var(--cardColor);
            }
          }

          .rating {
            position: absolute;
            top: 5%;
            right: 5%;
            background: var(--whiteColor);
            border-radius: var(--borderRadius);
            padding: 5px 10px;
            font-weight: 600;
            color: var(--textColor);

            .icon {
              font-size: var(--smallSize);
              color: orange;
            }
          }

          .imgDiv {
            justify-content: center;
            height: 150px;
            width: 100%;
            overflow: hidden;
            margin-top: 2rem;
            border-radius: var(--borderRadius);

            img {
              height: 100%;
              width: auto;
            }
          }

          .foodTitle {
            font-weight: 700;
            color: var(--textColor);
            font-size: var(--normalSize);
            padding: 1rem 0 0.5rem;
          }

          p {
            max-width: 200px;
            margin: auto;
            font-weight: 600;
            color: var(--textColor);
            opacity: 0.7;
          }

          .price {
            justify-content: space-between;
            width: 100%;
            margin: 2rem auto 0;
            background: var(--secondaryColor);
            padding: 0.5rem 1rem;
            border-radius: var(--borderRadius);

            span {
              font-weight: 600;
              font-size: var(--normalSize);
              color: var(--primaryColor);
            }

            .btn {
              background: var(--primaryColor);
              color: var(--whiteColor);
              justify-content: center;
              gap: 0.5rem;
              border: 1px solid transparent;

              &:hover {
                background: var(--secondaryColor);
                border: 1px solid var(--whiteColor);
                color: var(--primaryColor);
              }
            }
          }
        }
      }
    }

    .hide {
      display: none;
    }

    .live {
      animation: anim 0.5s ease;
    }
    @keyframes anim {
      0% {
        opacity: 0;
        transform: translateY(25px);
      }
      50% {
        opacity: 0;
        transform: translateY(15px);
      }
      75% {
        opacity: 0.3;
        transform: translateY(10px);
      }
      75% {
        opacity: 0.5;
        transform: translateY(5px);
      }
      100% {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
}

