// ======================== CHECKOUT  PAGE ==============================================>
.checkOut {
  padding-top: 6rem;
  .secTitle {
    .title {
      font-weight: 700;
      font-size: var(--mediumSize);
      padding-bottom: 1rem;
      color: var(--textColor);

      img {
        max-width: 30px;
        margin-left: 1rem;
      }
    }
  }

  .secContent {
    form {
      .mainContent {
        width: 100%;
        .title {
          color: var(--textColor);
          font-weight: 600;
          font-size: var(--normalSize);
          padding-bottom: 1rem;

          img {
            max-width: 30px;
            margin-left: 1rem;
          }
        }

        .input {
          label {
            display: block;
            padding-bottom: 5px;
            color: var(--textColor);
            font-size: var(--smallSize);
          }
        }

        .personalInfo,
        .deliveryAddress {
          .inputDiv {
            display: grid;
            align-items: center;
            gap: 1rem;
          }

          input,
          select,
          textarea {
            padding: 10px;
            border: 2px solid transparent;
            border-radius: 5px;
            background: #fdf6f4;
            width: 100%;
            color: #818181;

            &:focus {
              outline: none;
              border: 2px solid var(--secondaryColor);
            }

            &::placeholder {
              font-style: italic;
              opacity: 0.4;
              font-family: Cambria, Cochin, Georgia, Times, "Times New Roman",
                serif;
              font-size: 13px;
            }
          }

          textarea {
            height: 100px;
          }
        }

        .amountDiv {
          display: grid;
          .cartList {
            justify-content: space-between;
            margin-top: 0.5rem;
          }

          .btn {
            margin-top: 1rem;
            padding-top: 1rem;
            border: 1px solid var(--secondaryColor);
            background: var(--primaryColor);
            color: var(--whiteColor);
            width: 100%;
            text-align: center;
            font-weight: 600;
            font-size: var(--normalSize);
            &:hover {
              background: var(--secondaryColor);
              color: var(--primaryColor);
              border: 1px solid var(--primaryColor);
            }
          }
        }

        .paymentOption {
          .optionDiv {
            display: grid;
            row-gap: 1rem;
            .input {
              gap: 1rem;
              align-items: center;
              label {
                padding: 0;
              }

              .radio {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                input {
                  opacity: 1;
                }

                .after {
                  position: absolute;
                  border: 1px solid var(--primaryColor);
                  height: 15px;
                  width: 15px;
                  border-radius: 50%;
                  transition: 0.3s ease;
                }

                .after.active {
                  background: var(--primaryColor);
                }
              }
            }

            .TID,
            .tableNumber {
              padding: 0;
              transition: 0.5s ease;
              input {
                padding: 10px;
                border: 2px solid transparent;
                border-radius: 5px;
                background: var(--whiteColor);
                width: 100%;

                &:focus {
                  outline: none;
                  border: 2px solid var(--secondaryColor);
                }
              }
            }

            .TID,
            .tableNumber {
              display: none;
              overflow: hidden;
              height: 0;
            }
            .TIDActive,
            .tableNumberActive {
              display: block;
              height: max-content;
            }
          }
        }

        .cartDiv {
          width: 100%;
          .singleCart {
            padding-bottom: 1rem;
            width: 100%;
            justify-content: space-between;
            color: var(--textColor);
            img {
              width: 50px;
            }

            .foodDetails {
              flex: 1;
              margin-left: 1rem;

              span {
                justify-content: space-between;
              }

              .name_closeIcon {
                padding-bottom: 0.5rem;
                font-weight: 600;

                .deleteCartItem {
                  color: var(--primaryColor) !important;
                }
              }
            }
          }

          .singleCart:not(:last-child) {
            border-bottom: 1px solid var(--secondaryColor);
          }
        }
      }
    }
  }
}

