// ========================= LOGIN PAGE ========================================>
.loginPage {
  padding-top: 6rem;
  justify-content: center;
  position: relative;
  .pageContent {
    position: relative;
    text-align: center;
    border-radius: 1rem;
    padding: 2rem 1.5rem;
    width: 100%;
    max-width: 500px;
    background: var(--secondaryColor);
    border: 1px solid var(--whiteColor);
    overflow: hidden;

    .title {
      color: var(--textColor);
      padding-bottom: 0.5rem;
    }

    form {
      margin-top: 2rem;
      display: grid;
      gap: 1rem;
      z-index: 100;
      position: relative;

      .input {
        text-align: left;

        label {
          display: block;
          padding-bottom: 0.5rem;
          color: var(--textColor);
          font-size: 1rem;
          font-weight: 500;
          opacity: 0.8;
        }

        input {
          padding: 0.8rem 1rem;
          border: none;
          outline: none;
          border-radius: 5px;
          width: 100%;
          background: var(--cardColor);
          overflow: visible;
          font-weight: 600;
          color: var(--textColor);

          &::placeholder {
            color: var(--textColor);
            opacity: 0.5;
            font-weight: 400;
            font-size: 13px;
          }
        }
      }

      .btn {
        justify-content: center;
        gap: 1rem;
        font-size: 1.1rem;
        font-weight: 600;
        background: var(--primaryColor);
        color: var(--whiteColor);
        cursor: pointer;
        &:hover {
          background: var(--cardColor);
          color: var(--textColor);
        }

        .icon {
          transform: rotate(180deg);
        }
      }
    }

    .text {
      padding-top: 1rem;
      color: var(--textColor);
      font-weight: 500;
    }

    img {
      position: absolute;
      opacity: 0.2;
      width: 500px;
      top: 50%;
      transform: translate(-50%);
    }
  }

  .designImage1 {
    display: none;
    position: absolute;
    width: 100px;
    bottom: 2rem;
    right: 60%;
  }
  .designImage2 {
    display: none;
    position: absolute;
    width: 80px;
    top: 5rem;
    left: 60%;
  }
