    .sectionContent {
      grid-template-columns: repeat(4, 1fr);
      padding: 2.5rem 2rem;

      .singleCat {
        border: none !important;
      }
      .singleCat:nth-child(1) {
        padding-right: 2.5rem;
        border-right: 1px solid var(--secondaryColor) !important;
      }
      .singleCat:nth-child(2) {
        padding-right: 2.5rem;
        border-right: 1px solid var(--secondaryColor) !important;
      }
      .singleCat:nth-child(3) {
        padding-right: 2.5rem;
        border-right: 1px solid var(--secondaryColor) !important;
      }
    }
  }

  .about {
    .sectionContent {
      padding: 0 5rem;
      .aboutImage {
        width: 75%;
      }

      .aboutText {
        padding: 0 1rem 0 0;

        .title {
          font-size: 1.7rem;
          padding-bottom: 2rem;
        }
      }
    }
  }

  .review {
    .sectionContent {
      align-items: center;
      gap: 0;
      grid-template-columns: repeat(2, 1fr);
      padding: 0 0 0 8rem;
      .secText {
        .content {
          .customerDetails {
            .name {
              font-weight: 700;
              line-height: 25px;
              span {
                font-size: var(--smallSize);
              }
            }
          }

          .records {
            padding: 2rem 0.5rem;
            flex-direction: row;

            .leftDiv {
              gap: 1rem;
              img {
                max-width: 60px;
              }

              h1 {
                font-size: var(--mediumSize);
                color: var(--primaryColor);
                font-weight: 700;
              }

              span {
                color: var(--textColor);
                font-weight: 600;
                font-size: var(--smallSize);
              }
            }
          }
        }
      }

      .secImage {
        transform: translateY(-50px);
        img {
          width: 300px;
        }
      }
    }
  }

  .newsLetter {
    width: 70%;
    margin: auto;
  }

  .footer {
    .sectionContent {
      padding: 4rem 0;
    }
  }

  .menuPage {
    padding-top: 8rem;
    .secContent {
      .optionMenu {
        justify-content: center;
        gap: 1rem;

        .option {
          padding: 1rem 0;
          height: max-content;
          width: 80px;
        }
      }

      .allItems {
        .categoryWrapper {
          grid-template-columns: repeat(4, 1fr);

          .singleItem {
            .price {
              padding: 0.5rem;

              span {
                font-weight: 500;
              }
            }
          }
        }
      }
    }
  }

  .details {
    width: 70%;
  }

  .loginPage {
    padding-top: 8rem;

    .designImage1,
    .designImage2 {
      display: block;
    }
  }
}

::selection {
  background: var(--primaryColor);
  color: var(--whiteColor);
}

::-webkit-scrollbar {
  background: var(--secondaryColor);
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--primaryColor);
  border-radius: 10px;
}
